.group {
    background: yellow;
    width: 200px;
    height: 500px;
    .subgroup {
        background: orange;
        width: 150px;
        height: 200px;
    }
}

.fixed {
    position: fixed;
}


/* sidebar */

.bs-docs-sidebar {
    padding-left: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
    img {
        display: block;
        margin: 0 auto;
    }
    .nav {
        >li {
            margin: 10px 0;
        }
        >li>a {
            color: #999;
            border-left: 2px solid transparent;
            padding: 4px 20px;
            font-size: 13px;
            font-weight: 400;
        }
        .nav {
            >li>a {
                padding-top: 1px;
                padding-bottom: 1px;
                padding-left: 30px;
                font-size: 12px;
            }
            >.active {
                >a,
                &:hover>a,
                &:focus>a {
                    font-weight: 500;
                }
            }
        }
        > {
            .active>a {
                color: #563d7c;
                text-decoration: none;
                background-color: transparent;
                border-left-color: #563d7c;
            }
            li>a {
                &:hover,
                &:focus {
                    color: #563d7c;
                    text-decoration: none;
                    background-color: transparent;
                    border-left-color: #563d7c;
                }
            }
            .active {
                >a,
                &:hover>a,
                &:focus>a {
                    font-weight: 700;
                }
            }
        }
        ul.nav {
            display: none;
        }
        >.active>ul.nav {
            display: block;
        }
    }
}

.MainContent {
    margin-top: 50px;
}

.group,
.subgroup {
    padding-top: 50px;
    margin-top: -50px;
}

.side-nav {
    .main-nav:before {
        width: 100%;
        border-top: 1px solid #d2d3d4;
        content: '';
        display: block;
        padding: 3px 0;
        margin: 3px 0;
    }
    &.affix {
        top: 0;
        width: 255px;
    }
}